const parser = filename => {
  console.log('hi');
  //let pdfParser = new PDFParser();
  // let dataBuffer = fs.readFileSync(filename);

  // pdf(dataBuffer).then(function(data) {

  //     // number of pages
  //     console.log(data.numpages);
  //     // number of rendered pages
  //     console.log(data.numrender);
  //     // PDF info
  //     console.log(data.info);
  //     // PDF metadata
  //     console.log(data.metadata);
  //     // PDF.js version
  //     // check https://mozilla.github.io/pdf.js/getting_started/
  //     console.log(data.version);
  //     // PDF text
  //     console.log(data.text);

  // });

  // default render callback
  function render_page(pageData) {
    //check documents https://mozilla.github.io/pdf.js/
    let render_options = {
      //replaces all occurrences of whitespace with standard spaces (0x20). The default value is `false`.
      normalizeWhitespace: true,
      //do not attempt to combine same line TextItem's. The default value is `false`.
      disableCombineTextItems: false,
    };

    return pageData.getTextContent(render_options).then(function(textContent) {
      let lastX,
        text = '';
      // let flag = false;
      for (let item of textContent.items) {
        // if (item.str.includes('thermostat') || flag) {
        //     console.log(item.transform, item.str, flag);
        //     flag = true;
        // }
        if (lastX == item.transform[4] || !lastX) {
          text += ' ' + item.str;
        } else {
          text += '\n' + item.str;
        }
        lastX = item.transform[4];
      }
      return text + '\n';
    });
  }

  let options = {
    pagerender: render_page,
  };

  let header = [
    'Date',
    'Payee / Payer',
    'Type',
    'Reference',
    'Description',
    'Income',
    'Expense',
    'Balance',
  ];

  pdf(dataBuffer, options).then(function(data) {
    let lines = data.text.split('\n');
    let start = false;
    let row, headerIdx, hold, balance;
    let table = [];
    for (let l of lines) {
      let str = l.trim();

      if (start) {
        if (str.indexOf('Ending Cash Balance') === 0) {
          start = false;
          balance = null;
        } else if (balance == null) {
          balance = parseFloat(str.replace(/,/g, ''));
        } else if (!header.includes(str)) {
          if (/\d{2}\/\d{2}\/\d{4}/.test(str)) {
            if (row) table.push(row);
            row = {};
            headerIdx = 0;
          }
          // special case
          if (headerIdx == 3 && str.length > 15) headerIdx++;
          if (headerIdx > 4) str = parseFloat(str.replace(/,/g, ''));
          if (headerIdx == 7) {
            row[header[5 + (str < balance)]] = hold;
            balance = str;
          }
          if (headerIdx == 5) {
            hold = str;
            headerIdx += 2;
          } else {
            console.log(headerIdx);
            row[header[headerIdx++]] = str;
          }
        }
      } else if (str.indexOf('Beginning Cash Balance as') === 0) {
        start = true;
      }
    }
    console.log(table, balance);
  });
};

export { parser };
